import React from 'react'
import Image from '../Images/cse.jpeg'



const About = () => {
    return(
        <div className="About " id="About">
                <h2 className='grandtitle'>About</h2>
                <div className="container">
                <div className='row'>
                     <div className='aboutView col-12 col-md-6'>
                        <div className="aboutText ">
                            <h2 className="about__subtitle">What is CSE, What is AstroHack</h2>
                            <p className="about__text">AstroTech is an online event organized by CSE. It is a series of workshops and conferences  about the different technology that take place during a weekend where participants have the chance to learn more about different technology along with qualified speakers and trainers. The first edition of this event will be online, on CSE's social media, in 2nd april, 2021.</p>           
                        </div>      
                    </div> 
                    <div className='imageView col-12 col-md-6'>
                        <div className="image">
                            <img src={Image}    alt="" />
                        </div>  
                    </div>                          
                </div>
                </div>
        </div>
    )
}

export default About