import { faArrowLeft, faArrowRight, faMinus, faPlus } from '@fortawesome/free-solid-svg-icons'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'



const FAQ2 = () => {
    return(
        <div className='FAQ' id='FAQ'>
             <div className="titre"> 
             <h2 className='grandtitle'>FAQ</h2>
    <section>
        <div className="container">
         
            
            <div className="accordion">
                <div className="accordion-item" id="question1">
                    <a className="accoridon-link" href="#question1"  >
                        What is our event about?
                        <FontAwesomeIcon className='A' icon={faPlus} />
                        <FontAwesomeIcon className='B' icon={faMinus} />             
                    </a>
                    <div className="answer">

                        <p>
                        It is a series of workshops and conferences  about the different technology that take place during a weekend where participants have the chance to learn more about different technology along with qualified speakers and trainers. The first edition of this event will be online, on CSE's social media, in 2nd april, 2021.

                        </p>
                       
                    </div>
                </div>
                <div className="accordion-item" id="question2">
                    <a className="accoridon-link" href="#question2">
                        How can i regester in this event?
                        
                        <FontAwesomeIcon className='A' icon={faPlus} />
                        <FontAwesomeIcon className='B' icon={faMinus} /> 
                    </a>
                    <div className="answer">
                    
                        <p>
                        You can do that by filling this form. Once the registration is closed, we will contact you via the email address you provided and give you more details about the workshop and the Talks.

                        </p>
                       
                    </div>
                </div> <div className="accordion-item" id="question3">
                    <a className="accoridon-link" href="#question3">
                        Do i need to be a member to attend it ?
                        <FontAwesomeIcon className='A' icon={faPlus} />
                        <FontAwesomeIcon className='B' icon={faMinus} /> 
                    </a>
                    <div className="answer">
                        <p>
                        No, it’s an open event where everyone is welcome to attend. Feel free to interact with us during the fun activities and ask as many questions you like during the workshops and talks.

                        </p>
                    </div>
                </div> 
                <div className="accordion-item" id="question4">
                    <a className="accoridon-link" href="#question4">
                        Do i need to have a skills in this techno to join you?
                        <FontAwesomeIcon className='A' icon={faPlus} />
                        <FontAwesomeIcon className='B' icon={faMinus} /> 
                    </a>
                    <div className="answer">
                        <p>
                        The event includes a dedicated track for beginners where you’ll learn how to build a simple classifier and the mathematical concepts behind it. You can also check the talks and keynote to get an idea of the trending and recent topics around AI in 2020.
                        </p>

                    
                    </div>
                </div>

            </div>
        </div>

    </section>
     </div>
     </div>
    )
}

export default FAQ2